import React,{useState,useEffect,useContext} from 'react';
import './TasksView.css'
import "@progress/kendo-theme-material/dist/all.css";
import { Grid, GridColumn } from "@progress/kendo-react-grid";

const valuesCell = (props) => {
    return (
      <td><button className="btn btn-primary">Edit</button></td>
    )
} 

function TasksView(props) {
    const [tasks, setTasks] = useState([]);
    const authToken = props.authToken;
    const user = props.user;

    useEffect(() => {
        fetchTasks();
    }, []);

    const fetchTasks = async function(){
        const response = await fetch('/tasks',{
            method: 'GET', 
            headers: {
            'Content-Type': 'application/json',
             Authorization: authToken
            },
        });
        const taskData = await response.json();
        if(taskData.error){
            alert(taskData.error);
        }
        else{
            setTasks(taskData);
        }
    }

    const handleLogout = function(){
        props.handleLogout();
    };

    const grid = <Grid
    data={tasks}
    pageable={true}
    >
    <GridColumn field="taskName" title="Name" />
    <GridColumn field="taskCategory" title="Category" />
    <GridColumn field="taskDescription" title="Description" />
    <GridColumn field="taskValues" title="Description" cell={valuesCell}/>
  </Grid>

    return (
        <div className="tasks-page">
            <div className="row records">
                <div className="col-md-3 col-sm-3 col-lg-3">
                    <label className="records-lable">Records</label>
                </div>
                <div className="col-md-5 col-sm-5 col-lg-5"></div>
                <div className="col-md-2 col-sm-2 col-lg-2">
                    <button className="btn btn-primary">Create a Record</button>
                </div>
                <div className="col-md-2 col-sm-2 col-lg-2">
                    <button className="btn btn-primary" onClick={handleLogout}>Logout</button>
                </div>
            </div>
            {grid}
        </div>
    )
}

export default TasksView
